@import url(https://fonts.googleapis.com/css?family=Roboto);
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  height: 100%;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5em;

  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.Video {
  width: 460px;
  padding: 10px 10px 1px;
  margin: 10px;

  background: #000000;
}

.Video iframe {
  display: block;
  width: 440px;
  height: 240px;
  margin: 0 auto;

  border: none;
}

.DateTime {
  margin: 6px 0 0 0;

  font-size: 80%;
  text-align: right;

  color: #cccccc;
}

